import * as React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/lib/utils";

const buttonVariants = cva(
  "inline-flex items-center justify-center rounded-xl text-sm font-medium transition-all duration-200 disabled:pointer-events-none disabled:opacity-50 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring active:scale-[0.99]",
  {
    variants: {
      variant: {
        default:
          "bg-linear-to-r from-primary to-chart-1 text-primary-foreground shadow-[0_12px_28px_color-mix(in_oklab,var(--primary)_40%,transparent)] hover:brightness-105",
        secondary:
          "border border-border/70 bg-secondary/80 text-secondary-foreground shadow-[0_8px_20px_color-mix(in_oklab,var(--foreground)_8%,transparent)] hover:bg-secondary",
        outline:
          "border border-border/80 bg-background/75 text-foreground hover:bg-accent/80 hover:text-accent-foreground",
        ghost: "text-muted-foreground hover:bg-accent/70 hover:text-foreground",
      },
      size: {
        default: "h-10 px-4 py-2",
        sm: "h-9 px-3 text-xs",
        lg: "h-11 px-8",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  },
);

export interface ButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {}

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  ({ className, variant, size, ...props }, ref) => {
    return (
      <button
        className={cn(buttonVariants({ variant, size, className }))}
        ref={ref}
        {...props}
      />
    );
  },
);
Button.displayName = "Button";
